@import "_variables.scss";

@media only screen and (min-width: 700px) and (max-width: 1160px){
	body{
	    background-position: 50% 7%, 50% 17%, 50% 36.2%, 50% 62%, 50% 88%, center center;
	}

	.header__logo .header__logo-link img{
		height: 240px;
	    margin-top: -70px;
	    margin-bottom: -66px;
	}

	header{
		&:after{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: calc(100% - 1px);
			z-index: 1;
			display: block;
			background-image: url('../images/bg_head__persons.png');
			background-position: 54% 45%;
		    background-repeat: no-repeat;
		    background-size: 120%;
		}
	}

	.section-head{
		padding-top: 22px;
	}

	.game-detail-menu_submenu{
	    margin-left: -50px;
	}

	.game-detail-menu{
		ul {
			li{
				&.submenu-data {
					.submenu-data__info{
    					left: calc(100% - 72px);
					}
				}
			}
		}
	}

	.container-games{
		.games-container{
			max-width: 25%;
			margin-left: 0 !important;
			margin-right: 0 !important;

			&:nth-last-child(2){
				margin-right: auto !important;
			}

			&:first-of-type{
				margin-left: auto !important;
			}

			a{
				font-size: 54px !important;
			}
		}

		.games-container__detail{
			width: 728px;
		    max-width: 728px;
		    left: calc(50% - 364px);

			.games-container__detai-container{
				padding: 20px;
			}
		}
	}

	.section-head_scroll .container-games .games-container{
		.games-container{
			max-width: 25%;
			margin-left: 0 !important;
			margin-right: 0 !important;

			&:nth-last-child(2){
				margin-right: auto !important;
			}

			&:first-of-type{
				margin-left: auto !important;
			}

			a{
				font-size: 54px !important;
			}
		}

		.games-container__detail{
			width: 600px;
			max-width: 600px;
			left: calc(50% - 300px);

			.games-container__detai-container{
				padding: 20px;
			}
		}
	}

	.game-detail-menu ul li a{
		font-size: 14px;
		line-height: 18px;
	}

	.container-search {
	    padding-top: 12px;
	    padding-right: 0px;

	    button{
	    	top: 10px;
    		right: -4px;
    		background-clip: unset;
    		width: 23px;
    		height: 23px;
    		background-image: url('../images/search_apple.png');
    		background-size: contain;
    		background-position: center;
    		background-repeat: no-repeat;

    		i{
    			display: none;
    		}
	    }

	    .search-result{
	    	right: 0;
	    	top: 100%;
	    }
	}

	.language-container{
		padding-top: 11px;
    	padding-left: 0px;
	}

	.login-container{
		padding-top: 14px;
    	padding-left: 4px;
	}

	.hot-offers{
		margin-top: 50px;
    	margin-bottom: 70px;
	}

	.hot-offers__slider .item{
		height: 256px;
	}

	.hot-offers__slider .hot-offers__slider-text{
		bottom: 20px;
	    left: 20px;
	    max-width: 50%;

	    .slider-text__title{
	    	font-size: 18px;
	    	line-height: 20px;
	    }

	    .slider-text__text{
	    	font-size: 16px;
	    	line-height: 18px;
	    }
	}

	.choose-us{
		margin-top: 70px;

		.choose-left{
			max-width: 100%;
			margin-top: 30px;

			.choose-title{
				-ms-flex: 0 0 30%;
			    flex: 0 0 30%;
			    max-width: 30%;
			    margin-bottom: 10px;
			}

			.choose-container__inner{
				-ms-flex: 0 0 70%;
			    flex: 0 0 70%;
			    max-width: 70%;

			    a{
			    	width: 46%;
			    	margin: 0 2% 28px 2%;
			    	height: 50px;

			    	&:nth-of-type(2){
			    		padding-top: 23px;
			    	}

			    	&:nth-of-type(4){
			    	    padding-top: 10px;
			    	}
			    }
			}
		}
	}

	.publications {
	    margin-top: 50px;
	}

	.public-item__full .public-item__image {
	    width: 241px;
	    margin-bottom: 10px;
	}

	.reviews {
	    margin-top: 60px;
	}

	footer{
	    margin-top: 60px;
    	padding-top: 40px;
    	display: block;
    	float: left;

    	>.container{
    		padding-left: 0px;
    		display: block;

    		>.row{
    			display: block;
    			float: left;
    		}
    	}

    	.footer-copyright{
    		max-width: 23% !important;
    		float: left;
    	}

    	.footer-information{
    		max-width: 18% !important;
    		float: left;
    	}

    	.footer-info{
    		ul{
    			li{
    				font-size: 12px;
    				line-height: 12px;

    				a{
    					font-size: 12px;
    					line-height: 12px;
    				}
    			}
    		}
    	}

    	.footer-folow {
    		max-width: 15% !important;
    		float: left;
    	}

    	.footer-contact{
    		max-width: 25% !important;
    		float: left;
    	}

    	.footer-payment{
    		max-width: 19% !important;
    		float: left;

    		.footer-info ul li i {
    			font-size: 40px;
    		}
    	}

    	.footer-title{
    		margin-bottom: 10px;

    		p{
	    		font-size: 13px;
	    		line-height: 13px;
	    	}
	    }

	    .footer-info-copy{
	    	display: block;
	    	width: 100%;
	    	float: left;
	    }

	    .footer-info-copy p{
	    	font-size: 10px;
    		line-height: 12px;
	    }
	}

	.button__go-up{
		right: 20px;
		bottom: 20px;
		background-image: url('../images/bg_games.jpg');
    	background-size: cover;
    	color: $full_black;

    	&:hover{
    		color: $full_black;
    	}
	}

	.public-container{
		>.row{
			display: block;
			float: left;
		}

		.public-col{
			-ms-flex: 0 0 100%;
			width: 100%;
		    flex: 0 0 100%;
		    max-width: 100%;

		    >.row{
		    	width: 100%;

		    	.public-item{
		    		-ms-flex: 0 0 100%;
					width: 100%;
				    flex: 0 0 100%;
				    max-width: 100%;
		    	}
		    }
		}
	}
}










